                        'deliveryType': indexService,
                        'shipmentPrice': this.parcelPrice,
                        'shipmentDescription': this.shipmentDescription,
                        'sender': {
                            'senderDestID': cityID,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.senderCity,
                            'senderAddress': this.senderAddress,
                            'senderName': this.senderName,
                            'senderPhone': this.senderPhoneCleaned,
                            'senderEmail': this.senderEmail,
                            'senderOrganization': this.senderOrganization,
                            'senderAddressAddition': this.senderAddressAddition,
                            'senderAddressNumber': this.senderAddressNumber,
                            'senderBuildingNumber': this.senderBuildingNumber,
                            'senderEntranceNumber': this.senderEntranceNumber,
                            'senderFloorNumber': this.senderFloorNumber,
                            'senderApartmentNumber': this.senderApartmentNumber,
                        },
                        'recipient': {
                            'recipientDestID': this.officeDatabase[indexRespAddress].officeDestinationID,
                            'recipientOfficeID': this.officeDatabase[indexRespAddress].officeID,
                            'recipientAddress': this.officeDatabase[indexRespAddress].officeAddress,
                            'recipientName': this.recipientName,
                            'recipientPhone': this.recipientPhoneCleaned,
                            'recipientEmail': this.recipientEmail,
                            'recipientOrganization': this.recipientOrganization,
                        },
                        'promoCode': this.promoCode,
                    }
                } else if (indexService == 3) {
                    let selectedSenderCity = this.senderCity

                    let indexSendCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            return object.destinationName === selectedSenderCity;
                        }
                    });
                
                    let senderCityID = this.evropatDestinations[indexSendCity].destinationID

                    let selectedRecipientCity = this.recipientCity

                    let indexRespCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.recipientProvince && object.destinationName == this.recipientCity) {
                            return object.destinationName === selectedRecipientCity;
                        }
                    });
                
                    let recipientCityID = this.evropatDestinations[indexRespCity].destinationID

                    payload = {
                        'parcelInvoice': this.parcelInvoice,
                        'parcelType': indexType,
                        'parcelWeight': this.weight,
                        'deliveryType': indexService,
                        'shipmentPrice': this.parcelPrice,
                        'shipmentDescription': this.shipmentDescription,
                        'sender': {
                            'senderDestID': senderCityID,
                            'senderProvince': this.senderProvince,
                            'senderCity': this.senderCity,
                            'senderAddress': this.senderAddress,
                            'senderName': this.senderName,
                            'senderPhone': this.senderPhoneCleaned,
                            'senderEmail': this.senderEmail,
                            'senderOrganization': this.senderOrganization,
                            'senderAddressAddition': this.senderAddressAddition,
                            'senderAddressNumber': this.senderAddressNumber,
                            'senderBuildingNumber': this.senderBuildingNumber,
                            'senderEntranceNumber': this.senderEntranceNumber,
                            'senderFloorNumber': this.senderFloorNumber,
                            'senderApartmentNumber': this.senderApartmentNumber,
                        },
                        'recipient': {
                            'recipientDestID': recipientCityID,
                            'recipientProvince': this.recipientProvince,
                            'recipientCity': this.recipientCity,
                            'recipientAddress': this.recipientAddress,
                            'recipientName': this.recipientName,
                            'recipientPhone': this.recipientPhoneCleaned,
                            'recipientEmail': this.recipientEmail,
                            'recipientOrganization': this.recipientOrganization,
                            'recipientAddressAddition': this.recipientAddressAddition,
                            'recipientAddressNumber': this.international_recipientAddressNumber,
                            'recipientBuildingNumber': this.recipientBuildingNumber,
                            'recipientEntranceNumber': this.recipientEntranceNumber,
                            'recipientFloorNumber': this.recipientFloorNumber,
                            'recipientApartmentNumber': this.recipientApartmentNumber,
                        },
                        'promoCode': this.promoCode,
                    }
                }
                await this.finishParcel(payload);
                this.parcelDetails = false
                this.parcelFinish = true
                this.$vuetify.goTo('#parcelCreation')
            } else {
                void(0)
            }
        },

        ...mapActions(['payParcels']),
        async payUserParcels() {
            this.paymentLoading = true
            await this.payOrderParcel();
            this.parcelFinish = false
            this.parcelWaybill = true
            this.$vuetify.goTo('#parcelCreation')
        },

        async shipmentPrintA4() {
            const payload = {
                'shipmentBarcode': this.selectedWaybill,
                'printoutType': 'A4'
            }
            // console.log(payload)
            let response = await axios({
                        url: '/parcel/evropat/get-waybill',
                        method: 'POST',
                        responseType: 'blob',
                        data: payload,
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_a4" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        async shipmentPrintSticker() {
            const payload = {
                'shipmentBarcode': this.selectedWaybill,
                'printoutType': 'sticker'
            }
            // console.log(payload)
            let response = await axios({
                        url: '/parcel/evropat/get-waybill',
                        method: 'POST',
                        responseType: 'blob',
                        data: payload,
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_sticker" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        shipmentByHand() {
            this.instructionsWaybill = true
        },

        async getPaymentData() {
            try {
                const payload = {
                    'payment_price': this.parcelPrice,
                    'payment_description': 'PRATKABG.COM'
                }
                let response = await axios.post('/payment/transcard/create', payload)
                console.log(response)
                this.transcard_payment_encoded = response.data.tcf_data.payload.ENCODED
                this.transcard_payment_checksum = response.data.tcf_data.payload.CHECKSUM
                this.transcard_can_pay = false
                this.payTester()
                
            } catch (error) {
                this.parcelMessage = 'В момента имаме технически проблем или профилактика. Моля да ни извините за неудобството.'
            }
        },

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        async isPaymentDone() {
            if (this.awaitingPaymentConfirmation === true) {
                await this.sleep(3000);
                this.checkPaymentStatus();
            }
        },

        async checkPaymentStatus() {

        }

    },

    mounted() {
        this.getOffices()
        this.getDestinations()
        this.getAddresses()

        // Development
        // this.getPaymentData()
    },
};
</script>

