<script>
import { mapActions } from 'vuex';

  export default {
    name: 'Home',

    components: {
    },

    computed: {
        menuHiderIcon() {
            if (this.toggleMini == false) { return "mdi-chevron-left" }
            else { return "mdi-chevron-right"}
        },
        mini: {
            get: function () {
                return (this.$vuetify.breakpoint.smAndDown) || this.toggleMini
            },
            set: function () {
                return (this.$vuetify.breakpoint.smAndDown) || this.toggleMini
            }
        },
        toggleTheme_text() {
            return !this.$vuetify.theme.dark ? 'Go Dark' : 'Go Light'
        },
        email() {
            const name = this.$store.getters.stateUser.email
            return name
        },
    },

    data: () => ({
      sidebarMenu: true,
      toggleMini: false,
      switch1: true,
      switch2: false,
      Logout_btn: 'Излез',

      itemsProfile: [
        { title:"Profile", href:"/home", icon:"mdi-account" },
        { title:"Addresses", href:"/profile/address", icon:"mdi-map-marker" },
        { title:"Payment Options", href:"/profile/payment", icon:"mdi-credit-card-settings-outline" },
