<template>
    <v-container>
      <v-row
        align='center'
        justify='center'
      >
        <v-col>

          <v-card
            class="mx-auto"
            max-width="100%"
            rounded
          >
            <v-list-item two-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  Profile
                </div>
                <v-list-item-title class="text-h5 mb-1">
                  User Profile
                </v-list-item-title>
                <v-list-item-subtitle>Profile Data</v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-avatar
                tile
                size="80"
              >
              <v-icon size="50">mdi-account</v-icon>
              </v-list-item-avatar>
            </v-list-item>

          </v-card>

        </v-col>


      </v-row>
    </v-container>

</template>

<script>
// import axios from 'axios'
import { mapActions } from 'vuex'

  export default {
    name: 'RegisterDevice',

    components: {
    },

    data: () => ({
        headers: [
          {
            text: 'Access Event',
            align: 'start',
            filterable: false,
            value: 'event',
          },
          { text: 'Accessed Room', value: 'room' },
          { text: 'User', value: 'user' },
          // { text: 'Biometric ID', value: '_bio_id' },
          { text: 'Time', value: 'time' },
          { text: 'Date', value: 'date' },
        ],
    }),

    computed: {
      organization_users() {
        const count = this.$store.getters.stateUser.current_users + '/' + this.$store.getters.stateUser.max_users
        return count
      },
    },

    methods: {
      ...mapActions(['viewMe']),
      async getOrganization(){
        await this.viewMe()
      },

      chip_color(event_status) {
        let color = 'primary'

        if (event_status == 'AccessGranted') {
          color = 'success'
          return color
        }

        else if (event_status == 'AccessDenied') {
          color = 'error'
          return color
        }

        else if (event_status == 'TryAgain') {
          color = 'warning'
          return color
        }
        
        else if (event_status == 'NotRegistered') {
          color = 'info'
          return color
        }

      },
    },

    mounted() {
      this.getOrganization()
    },
  }
</script>
