<script>
import axios from 'axios';

  export default {
    fName: 'AdminParcels',

    components: {
    },

    data () {
      return {
        section_name: 'Активни международни пратки',
        section_name_domestic: 'Активни вътрешни пратки',
        data_loading: true,
        search: '',
        expanded_international: [],
        expanded_domestic: [],
        singleExpand: true,
        parcel_database_headers: [
          {text: 'Завърши', value: 'completed'},
          {
            text: 'Номер на поръчка',
            align: 'start',
            filterable: false,
            value: 'orderNumber',
          },
          { text: 'Създадена на', value: 'dateCreated' },
          { text: 'Подател', value: 'senderDetails.senderName' },
          { text: 'Телефон Подател', value: 'senderDetails.senderPhone' },
          { text: '', value: 'data-table-expand' },
        ],


        // Data
        international_database_data: [],
        domestic_database_data: [],
      }
    },
   methods: {
      getTransport(value) {
        if(value == 2) {
          return 'От офис'
        }
        else {
          return 'От адрес'
        }
      },

      async getDomesticParcelDatabase(){
        const dataDB = await axios.get('/admin/parcels/domestic')
        // console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.domestic_database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.domestic_database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }

      },

      async getInternationalParcelDatabase() {
        const dataDB = await axios.get('/admin/parcels/international')
        // console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.international_database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }
      },

      getColor (parcelType) {
        if (parcelType == 2) return 'primary'
        else if (parcelType == 4) return 'secondary'
        else return 'green'
      },

      getColorTransport (transportedBy) {
        if (transportedBy == 'Evropat') return 'red'
        else if (transportedBy == 'Direx') return 'dark-blue'
        else if (transportedBy == 'Econt') return 'blue'
        else return 'green'
      },

      async updateParcel(item) {
        const payload = {
          'orderNumber':item.orderNumber,
        }
        await axios.post('/admin/parcels/finish', payload)
      }

    },
    mounted(){
      this.getDomesticParcelDatabase()
      this.getInternationalParcelDatabase()
    },
  }
</script>
